.tRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-height: 40px;
  border-bottom: 2px solid #bfbfbf;
}

.data {
  flex: 1;
  max-width: 155px;
  text-align: start;
  word-wrap: break-word;
}

.btn {
  flex: 1;
  max-width: 155px;
  text-align: center;
}

.xBtn {
  margin: 0;
  padding: 2px 8px;
  border: none;
  border-radius: 10%;
  text-align: center;
  background: #ee6622;
  color: #fff;
}
