.container {
  display: flex;
  justify-content: center;
}

.container .table {
  display: flex;
  flex-flow: column nowrap;
  flex-basis: 98%;
  padding: 40px;
  margin: 30px 0;
  border: 1px solid #f1f1f1;
  border-radius: 4px;
  box-shadow: 4px 4px 10px #999999;
}

.thead {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

.tbody {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

.thead .tr {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
}
.th {
  flex: 1;
  max-width: 155px;
  color: #e6541a;
  font-size: 16pt;
  text-align: start;
}
.btn {
  flex: 1;
  max-width: 155px;
  color: #e6541a;
  font-size: 16pt;
  text-align: center;
}

.addBtn {
  align-self: flex-end;
  height: 50px;
  width: 50px;
  margin: 15px 50px;
  border: none;
  border-radius: 50%;
  background-color: #e6541a;
  color: #f1f1f1;
  font-size: 33pt;
  text-align: center;
}

.addBtn:hover {
  background-color: #ec8027;
}
