.container {
  display: flex;
  justify-content: center;
}

.container .table {
  display: flex;
  flex-flow: column nowrap;
  flex-basis: 98%;
  padding: 40px;
  margin: 30px 0;
  border: 1px solid #f1f1f1;
  border-radius: 4px;
  box-shadow: 4px 4px 10px #999999;
}

.thead {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

.tbody {
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

.thead .tr {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
}
.th {
  flex: 1;
  max-width: 104px;
  padding: 10px;
  color: #e6541a;
  font-size: 16pt;
  text-align: start;
}

.btn {
  flex: 1;
  max-width: 104px;
  padding: 10px;
  color: #e6541a;
  font-size: 16pt;
  text-align: center;
}
